<template>
	<div class="result-container">
		<div class="result-header">
			<div class="result-felx">
				<div class="result-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="result-gong">第{{url_data.now}}题（共{{url_data.total}}题）</div>
				<div class="result-jieguo">测试结果</div>
			</div>
		</div>
		<div class="result-content">
			<div v-for="item in eqresults_list">
				<div class="result-daant">{{item.eqresult_title}}</div>
				<div class="result-daan">{{item.eqresult_content}}</div>
			</div>
		</div>
		<div class="result-footer">
			<div class="result-tuichu" @click="go_eqtest()">退出</div>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				point: '',
				eqtestid: '1',
				eqresults_list: '',
				data: [],
				url_data:{}
			}
		},
		methods: {
			back(){
				window.history.back();
			},
			go_eqtest(){
				this.$router.push('/eqtest')
			},
			eqresults() {
				var that = this;
				var params = {
					eqtestid: that.url_data.id,
					point: that.url_data.point,
				};
				that.$http.post('/eqtest/eqresults', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.eqresults_list = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.url_data ={
				id: this.$route.params.id,
				point: this.$route.params.point,
				now: this.$route.params.now,
				total: this.$route.params.total
			};
			this.eqresults();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.result-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.result-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.result-around {
		display: flex;
		justify-content: space-around;
	}
	
	.result-felx {
		display: flex;
	}
	
	.result-fanhui img {
		width: auto;
		height: 1.5rem;
		margin-left: 1.5rem;
		margin-top: 1.5rem;
	}
	
	.result-gong {
		font-size: 1.2rem;
		padding-top: 0.2rem;
		padding-left: 1rem;
	}
	
	.result-jieguo {
		font-size: 1.8rem;
		padding-left: 3rem;
	}
	.result-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
		background: #FFFFFF;
	}
	.result-daant{
		font-size: 1.8rem;
		padding-top: 3rem;
		padding-left: 2rem;
		color: #ff4081;
	}
	.result-daan{
		font-size: 1.5rem;
		padding-top: 1.5rem;
		padding-left: 2rem;
		padding-bottom: 2rem;
		color: #ff4081;
	}
	.result-footer {
		height: 55px;
		line-height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #ff4081;
	}
	.result-tuichu{
		font-size: 1.5rem;
		color: #FFFFFF;
		text-align: center;
	}
</style>


